import React, { ReactElement, FC, useMemo, forwardRef, useImperativeHandle } from 'react';
import { Row, Col, Button } from 'antd';
import moment from 'moment';
import style from './index.less';
import { useGetProductionDetail } from '@/services/publicServices';
import { ProductionAdjustStatus } from '@/components/miniComponents';

export interface Props {
  productionId: string;
  btnRender?: (score: number) => any;
  imgSpan?: number;
  detailSpan?: number;
  btnSpan?: number;
}


const CommentProductionDetail: FC<Props> = (props: Props, ref: any) => {
  const { productionId, btnRender, imgSpan, detailSpan, btnSpan } = props;
  const { productionDetail, detailLoading, mutate } = useGetProductionDetail(productionId);
  const coverImage = useMemo(() => {
    return productionDetail?.coverImage && JSON.parse(productionDetail?.coverImage);
  }, [productionDetail]);

  useImperativeHandle(ref, () => ({
    refreshDetail: mutate,
    productionDetail,
  }));

  return (
    <Row>
      <Col span={ imgSpan ?? 4 }>
        {coverImage?.length ? <img className={ style.productionImg } src={ coverImage[0] } /> : ''}
      </Col>
      <Col span={ detailSpan ?? 7 } className={ style.productionInfo }>
        <h3>{productionDetail?.title}</h3>
        <p style={{ fontSize: 16 }}>大众评分：<strong>{Number(productionDetail?.publicScore).toFixed(1)}</strong></p>
        <p>专业评分：{Number(productionDetail?.performanceScore).toFixed(1)}</p>
        <p>数据状态：<ProductionAdjustStatus subjectId={ productionId } /></p>
      </Col>
      <Col span={ btnSpan ?? 10 }>
        {btnRender && btnRender(productionDetail?.performanceScore)}
      </Col>
    </Row>
  );
};

export default forwardRef(CommentProductionDetail);
