import React, { FC, useMemo, useState, useCallback } from 'react';
import { Link } from 'umi';
import { Pagination, Select, Spin, Space } from 'antd';
import { Role } from '@/utils/authority';
import { withAuthorized } from '@/utils/Authorized';
import CommentProductionDetail from '@/components/CommentProductionDetail';
import { CommentStatusMaps, SortOptionsMaps } from '@/utils/constant';
import CommonItemWrap from '@/components/CommonItemWrap';
import style from './index.less';
import { useGetCommontList } from '@/services/publicServices';

const PAGESIZE = 10;

const ProductionCommontList = (props: any) => {

  const { subjectId, commentUserRank } = useMemo(() => {
    return {
      subjectId: props?.match?.params?.id,
      commentUserRank: props?.location?.query?.commentUserRank
    };
  }, [props]);

  const initSearch = {
    pagination: { pageSize: PAGESIZE, current: 1 },
    query: {
      subjectId,
      commentUserRank,
      rootId: 0
    },
    sort: {
      key: 'createTime',
      type: 'desc'
    }
  };
  const [searchParams, setSearchParams] = useState(initSearch);
  const { commontList, pagination, isValidating, mutate, CommontCount } = useGetCommontList(searchParams);

  const handleChangePage = useCallback((current: number) => {
    setSearchParams((preParams: any) => ({
      ...preParams,
      pagination: { pageSize: PAGESIZE, current },
    }));
  }, []);

  const handleSelect = useCallback((value: string) => {
    setSearchParams((preParams: any) => ({
      ...preParams,
      query: {
        commentUserRank,
        checkStatus: value,
        subjectId
      }
    }));
  }, [commentUserRank, subjectId]);

  const getIsExpert = useCallback((thisCommentUserRank: string) => {
    return thisCommentUserRank.indexOf('expert') >= 0;
  }, []);

  const isExportCommon = useMemo(() => {
    return commentUserRank === 'expert';
  }, [commentUserRank]);

  const handleChangeSort = useCallback((value: string) => {
    setSearchParams((preParams: any) => ({
      ...preParams,
      sort: {
        key: value,
        type: 'desc'
      }
    }));
  }, []);

  return (
    <div className={ style.productionCommontListWrap }>
      <div className={ style.productionCommontHead }>
        <CommentProductionDetail productionId={ subjectId } />
      </div>
      <Spin tip="加载中..." spinning={ isValidating }>
        <div className={ style.productionCommontContent }>
          <div className={ style.productionCommontChunkHead }>
            <Space>
              <span>{isExportCommon ? '专家' : '大众'}评论</span>
              <Select value={ searchParams?.sort?.key } options={ SortOptionsMaps } onSelect={ handleChangeSort } />
            </Space>
            <Select placeholder="请选择评论状态" style={{ width: 150 }} allowClear={ true } options={ CommentStatusMaps } onChange={ handleSelect } />
          </div>
          {commontList?.map((item: any, index: number) => {
            return <CommonItemWrap showExpertIdentity={ isExportCommon } showBlackBtn={ !getIsExpert(item?.commentUserRank) } key={ index } common={ item } CommontCount={ CommontCount } refresh={ mutate } />;
          })}
          <div className={ style.footerPage }>
            <Pagination total={ Number(pagination?.total) }
              showTotal={ (total: number) => `总计 ${ total } 条` }
              pageSize={ Number(pagination?.pageSize || PAGESIZE) }
              current={ Number(pagination?.current) }
              showQuickJumper={ true }
              onChange={ handleChangePage }
            />
          </div>
        </div>
      </Spin>
    </div>
  );
};

export default withAuthorized(ProductionCommontList, [Role.SYSTEM, Role.COMMENT, Role.EXPERT]) ;
